import xs from 'xstream';
import {run} from '@cycle/xstream-run';
import {button, h1, h4, a, div, makeDOMDriver} from '@cycle/cycle-dom';

function main() {
    return {
        DOM: Rx.Observable.of(
            div([
                button('.refresh', 'Refresh'),
                div('.results', [
                    h1('.user-name', '(name)'),
                    h4('.user-url', '(url)'),
                    button('.follow-btn', 'Follow')
                ])
            ])
        )
    };
}

const drivers = {
    DOM: makeDOMDriver('#app')
};

run(main, drivers);



